:root {
  --light-color: #f4f4f4;
  --dark-color: #2e2e2e;
  --main-color: #111321;
  --secondary-color: #4440f6;
  --duration: 250ms;
  --timeFunc: cubic-bezier(.4, 0, .2, 1);
  --light-theme-color: #f4f4f4;
  --primary-text-color: #111321;
  --secondary-text-color: rgba(17, 19, 33, .4);
  --accent-color: #4440f6;
  --dark-theme-color: #2e2e2e;
  --header-border-bottom-color: rgba(0, 0, 0, .2);
}

.filter-wrapper {
  column-gap: 10px;
  margin-top: 30px;
  margin-bottom: 60px;
  display: flex;
  position: relative;
}

.filter-wrapper--categories {
  flex-basis: 80%;
}

.filters {
  flex-wrap: wrap;
  display: flex;
}

.filters--category {
  text-align: center;
  color: var(--secondary-color);
  cursor: pointer;
  border: 1px solid var(--secondary-color);
  transition: all var(--duration) var(--timeFunc);
  border-radius: 100px;
  padding: 8px 24px;
  font-size: 12px;
  line-height: 1.33;
  display: inline-block;
}

.filters--category:not(:last-child) {
  margin-right: 16px;
}

.filters--category:hover, .filters--category:focus {
  color: var(--light-color);
  background-color: var(--secondary-color);
}

.filters--others {
  opacity: 0;
  z-index: -10;
  filter: drop-shadow(0 10px 60px rgba(0, 0, 0, .1));
  color: var(--secondary-color);
  transition: all var(--duration) var(--timeFunc);
  background: #f8f8f8;
  border-radius: 20px;
  flex-direction: column;
  row-gap: 12px;
  padding: 14px 24px;
  font-size: 12px;
  line-height: 1.33;
  display: inline-flex;
  position: absolute;
  top: 102%;
}

.filters--others.isOpen {
  z-index: 10;
  opacity: 1;
}

.filters--others .other-category {
  cursor: pointer;
  transition: all var(--duration) var(--timeFunc);
}

.filters--others .other-category:focus, .filters--others .other-category:hover {
  text-decoration: underline;
}

.filters .active-category {
  color: var(--light-color);
  background-color: var(--secondary-color);
}

.header {
  width: 100%;
  z-index: 50;
  background: var(--light-color);
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.header:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
}

.header__body {
  height: 81px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__logo {
  z-index: 3;
  cursor: pointer;
  font-weight: 700;
  line-height: 1.375;
  position: relative;
}

.header__list {
  z-index: 2;
  gap: 30px;
  display: flex;
  position: relative;
}

.header__list-icon {
  display: none;
}

.header__link {
  color: var(--primary-text-color);
  transition: all var(--duration) var(--timeFunc);
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  padding: 10px 3px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.375;
}

.header__link:hover, .header__link:focus {
  border-color: var(--accent-color);
}

.header__search {
  z-index: 3;
  height: 31px;
  outline: 1px solid var(--primary-text-color);
  border-radius: 30px;
  align-items: center;
  padding: 10px 6px;
  display: flex;
}

.header__search i {
  color: var(--primary-text-color);
  font-size: 20px;
}

.header__search input {
  background-color: var(--light-theme-color);
  height: 40px;
  border: none;
  outline: none;
  flex: 1;
  padding-left: 10px;
  font-size: 18px;
}

.search__input input::-ms-input-placeholder {
  color: var(--secondary-text-color);
}

.search__input input::placeholder {
  color: var(--secondary-text-color);
}

.header__burger {
  display: none;
}

@media screen and (max-width: 767px) {
  body.lock {
    overflow: hidden;
  }

  .header__body {
    height: 106px;
  }

  .header__logo {
    font-size: 24px;
  }

  .header__menu {
    width: 100%;
    height: 100%;
    background-color: var(--light-theme-color);
    padding: 125px 0 0;
    transition: all .5s ease-in-out;
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
  }

  .header__menu.active {
    top: 0;
  }

  .header__list, .header__list-icon {
    display: block;
  }

  .header__list li {
    align-items: center;
    gap: 14px;
    padding-top: 6px;
    padding-bottom: 6px;
    padding-left: 16px;
    display: flex;
  }

  .header__list li:focus {
    background-color: var(--accent-color);
  }

  .header__link {
    font-size: 16px;
    font-weight: 500;
  }

  .header__search {
    z-index: 3;
  }

  .header__burger {
    cursor: pointer;
    width: 30px;
    height: 24px;
    z-index: 3;
    display: block;
    position: relative;
  }

  .header__burger span {
    background-color: var(--primary-text-color);
    width: 100%;
    height: 3px;
    border-radius: 2px;
    transition: all .5s ease-in-out;
    position: absolute;
    top: 11px;
    left: 0;
  }

  .header__burger:before, .header__burger:after {
    content: "";
    background-color: var(--primary-text-color);
    width: 100%;
    height: 3px;
    border-radius: 2px;
    transition: all .5s ease-in-out;
    position: absolute;
    left: 0;
  }

  .header__burger:before {
    top: 0;
  }

  .header__burger:after {
    bottom: 0;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active:before {
    top: 11px;
    transform: rotate(45deg);
  }

  .header__burger.active:after {
    bottom: 11px;
    transform: rotate(-45deg);
  }
}

@media screen and (min-width: 768px) {
  .header__body {
    height: 113px;
  }

  .header__logo {
    font-size: 32px;
  }

  .header__search {
    width: 213px;
  }
}

@media screen and (min-width: 1280px) {
  .header__search {
    width: 288px;
  }
}

.card-container {
