.card__date-creation {
  flex-grow: 1;
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.card__date-creation .card__date {
  color: #a8a8a8;
  font-size: 16px;
  font-weight: 1.375;
}

.card__date-creation .card-read-more {
  color: var(--secondary-color);
  transition: all var(--duration) var(--timeFunc);
  font-size: 16px;
  line-height: 1.375;
}

.card__date-creation .card-read-more:hover, .card__date-creation .card-read-more:focus {
  text-decoration: underline;
}

